body, html{
  width: 100%;
  height: 100%;
  font-family: 'Helvetica Neue';
  font-size: 14px;
}


*{
  box-sizing: content-box;
}

#root{
  width: 100%;
  height: 100%;
  position: relative;
}

.rainbow-text {
  background-image: linear-gradient(to left, violet, indigo, blue, green, #ffa723, orange, red);   
  -webkit-background-clip: text;
  animation-name: rainbow;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  color: transparent;
  font-weight: 900;
}

@keyframes rainbow {
  0%{
    background-image: linear-gradient(to left, violet, indigo, blue, green, #ffa723, orange, red);
  }
  14%{
    background-image: linear-gradient(to left, red, violet, indigo, blue, green, #ffa723, orange);
  }
  28%{
    background-image: linear-gradient(to left, orange, red, violet, indigo, blue, green, #ffa723);
  }
  42%{
    background-image: linear-gradient(to left, #ffa723, orange, red, violet, indigo, blue, green);
  }
  56%{
    background-image: linear-gradient(to left, green, #ffa723, orange, red, violet, indigo, blue);
  }
  70%{
    background-image: linear-gradient(to left, blue, green, #ffa723, orange, red, violet, indigo);
  }
  84%{
    background-image: linear-gradient(to left,  indigo, blue, green, #ffa723, orange, red, violet);
  }
  /* 100%{
    background-image: linear-gradient(to left, violet, indigo, blue, green, #ffa723, orange, red);
  } */
}